// layouts/AuthLayout.tsx
import React from 'react';
// 필요한 모듈들을 한번에 import
import {
  View,
  StyleSheet,
  Platform,
  ScrollView,
  KeyboardAvoidingView,
  TouchableWithoutFeedback,
  Keyboard,
  ImageBackground,
  Image,
  StatusBar,
  Text
} from 'react-native';

// useTheme 기능
const useTheme = () => ({
  theme: {
    colors: {
      background: '#ffffff',
    },
    dark: false
  }
});

// LoadingIndicator 컴포넌트 간단히 구현
const LoadingIndicator = () => <View testID="loading-indicator"><Text>로딩중...</Text></View>;

// SafeAreaView 간단 구현
const SafeAreaView = ({ children, style }) => <View style={style}>{children}</View>;

interface AuthLayoutProps {
  children: React.ReactNode;
  loading?: boolean;
  title?: React.ReactNode;
  footer?: React.ReactNode;
  imageBackground?: boolean;
  logoVisible?: boolean;
}

const AuthLayout: React.FC<AuthLayoutProps> = ({
  children,
  loading = false,
  title,
  footer,
  imageBackground = false,
  logoVisible = true,
}) => {
  const { theme } = useTheme();
  
  // Keyboard.dismiss를 안전하게 호출
  const dismissKeyboard = () => {
    if (typeof Keyboard !== 'undefined' && typeof Keyboard.dismiss === 'function') {
      Keyboard.dismiss();
    }
  };
  
  const renderContent = () => (
    <KeyboardAvoidingView
      behavior={Platform.OS === 'ios' ? 'padding' : 'height'}
      style={styles.keyboardView}
    >
      <TouchableWithoutFeedback onPress={dismissKeyboard}>
        <ScrollView
          contentContainerStyle={styles.scrollView}
          keyboardShouldPersistTaps="handled"
        >
          {/* 로고 */}
          {logoVisible && (
            <View style={styles.logoContainer}>
              <Image
                // 앱 로고 이미지 경로 설정 
                source={{}} // 테스트용 빈 객체
                style={styles.logo}
                testID="logo-image"
              />
            </View>
          )}
          
          {/* 제목 */}
          {title && <View style={styles.titleContainer}>{title}</View>}
          
          {/* 메인 콘텐츠 */}
          <View style={styles.contentContainer}>
            {loading ? <LoadingIndicator /> : children}
          </View>
          
          {/* 푸터 */}
          {footer && <View style={styles.footerContainer}>{footer}</View>}
        </ScrollView>
      </TouchableWithoutFeedback>
    </KeyboardAvoidingView>
  );
  
  // 배경 이미지 사용 여부에 따라 다른 레이아웃 반환
  if (imageBackground) {
    return (
      <SafeAreaView style={styles.container}>
        <StatusBar
          backgroundColor="transparent"
          translucent
          barStyle="light-content"
        />
        <ImageBackground
          source={{}} // 테스트용 빈 객체
          style={styles.backgroundImage}
          testID="image-background"
        >
          <View style={styles.overlay}>
            {renderContent()}
          </View>
        </ImageBackground>
      </SafeAreaView>
    );
  }
  
  return (
    <SafeAreaView style={styles.container}>
      <StatusBar
        backgroundColor={theme.colors.background}
        barStyle={theme.dark ? 'light-content' : 'dark-content'}
      />
      {renderContent()}
    </SafeAreaView>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  backgroundImage: {
    flex: 1,
    width: '100%',
    height: '100%',
  },
  overlay: {
    flex: 1,
    backgroundColor: 'rgba(0, 0, 0, 0.5)',
    paddingTop: Platform.OS === 'android' ? 30 : 0,
  },
  keyboardView: {
    flex: 1,
  },
  scrollView: {
    flexGrow: 1,
    paddingHorizontal: 20,
    paddingBottom: 20,
  },
  logoContainer: {
    alignItems: 'center',
    marginTop: 50,
    marginBottom: 30,
  },
  logo: {
    width: 150,
    height: 150,
  },
  titleContainer: {
    marginBottom: 30,
  },
  contentContainer: {
    flex: 1,
    width: '100%',
  },
  footerContainer: {
    width: '100%',
    marginTop: 20,
    alignItems: 'center',
  },
});

export default AuthLayout;